import { config } from "./core/constants";
import { IRemoteConfig } from "./core/interfaces/config";

const wind = window as any
const fbox = wind.Featurebox({ config })

fbox.pubsub.subscribe(fbox.FETCHED_DATA, (data: any) => {
  const remoteConfig:IRemoteConfig = data as IRemoteConfig
  localStorage.setItem("remoteData", JSON.stringify(data))
})